body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html {
  background: -webkit-gradient(linear, left top, left bottom, from(#fc466b), to(#3f5efb)) no-repeat center center fixed;
  background: -webkit-linear-gradient(#fc466b, #3f5efb) no-repeat center center fixed;
  background: -o-linear-gradient(#fc466b, #3f5efb) no-repeat center center fixed;
  background: linear-gradient(#fc466b, #3f5efb) no-repeat center center fixed;
}

::-webkit-input-placeholder {
    color: white;
}

:-ms-input-placeholder {
    color: white;
}

::-ms-input-placeholder {
    color: white;
}

::placeholder {
    color: white;
}

.form {
    width: 50%;
    margin: auto;
}

@media only screen and (max-width: 768px) {
    .form {
        width: 100%;
    }
}



